@media only screen and (orientation:landscape) {
    .lyrics {
        color: rgb(197, 197, 197);
        background-color: #222222;
        text-align: center;
        margin: auto;
        padding: 0;
        overflow-y: hidden;
        height:75vh;
        line-height: 20px;
        width:100%;
        position:relative;
        text-shadow:1.5px 1.5px .5px black;
    }

    .lyrics p {
        font-size: 20px;
        margin-bottom:-10px;
    }

    .lyrics p.bold {
        font-weight: bold;
        font-size: 30px;
    }

    .song_info {
        text-shadow:2px 2px 2px black;
        color: white;
        background-color:#1f1f1f;
        box-shadow: 0px -3px 5px rgba(0, 0, 0, 0.082);
        height:25vh;
        width: 100%;
        display:flex;
        z-index: 100;
        position:relative;
    }

    .song_info .song-info-container {
        display:flex;
        float:left;
        height:50%;
        margin:auto;
        position: relative;
        text-align: center;
    }
    .song_info .song-info-container .album_img {
        height:100%;
        float:left;
    }

    .song_info .song-info-container .song-info-container2 {
        float:left;
        flex-grow:100;
        height:100%;
    }
    .song_info .song-info-container .song-info-container2 p {
        height:50%;
        width:100%;
        margin:0;
        float: left;
    }
    .song_info .song-info-container p.song_title {
        font-size: 30px;
    }
    .song_info .song-info-container p.artists {
        font-size: 20px;
    }


    .song_info .music-player {
        position:absolute;
        left:0;
        bottom:0;
        width:100%;
        height:23%;
        margin:0;
        padding:0;
    }

    .song_info .music-player .progress-bar {
        position:absolute;
        width: 100%;
        height:10px;
        margin:0;
        bottom:0;
        left:0;
        appearance: none;
        -webkit-appearance: none;
        background-color:rgba(0, 65, 5, 0.952);
    }
    .song_info .music-player .progress-bar-filled {
        position: absolute;
        height:10px;
        margin: 0;
        bottom:0;
        left:0;
        background-color:rgb(51, 255, 0);
    }
    .song_info .music-player .progress-bar::-webkit-slider-thumb {
        -webkit-appearance: none;
        appearance: none;
    }
    .song_info .music-player .progress-bar::-moz-range-thumb {
        appearance: none;
    }

    .song_info .music-player button {
        background: none;
        cursor: pointer;
        height:35px;
        width:35px;
        border:none;
        position: absolute;
        padding: 7px;
        outline: none;
    }

    .song_info .music-player button.pausebtn {
        border: 1px solid white;
        border-radius: 100%;
        left:49%;
    }
    .song_info .music-player button.skipnextbtn {
        left:53%;
        height:35px;
        width:35px;
    }
    .song_info .music-player button.skippreviousbtn {
        left:45%;
        transform: scaleX(-1);
    }
    .song_info .music-player button img {
        height:100%;
    }

    .song_info .music-player button.pausebtn img {
        position:relative;
        left:2px;
        height:100%;
    }

    ::-webkit-scrollbar {
        display: none;
    }
}